html {
  box-sizing: border-box;
  font-size: 100%; /* 16px */
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

body {
  font-family: "Sora";
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Sora";
  font-weight: 700;
}

h1 {
  font-size: 4.21rem; /* 67.36px */
}

h2 {
  font-size: 3.158rem; /* 50.56px */
}

h3 {
  font-size: 2.369rem; /* 37.92px */
}

h4 {
  font-size: 1.777rem; /* 28.48px */
}

h5 {
  font-size: 1.333rem; /* 21.28px */
}

small {
  font-size: 0.75rem; /* 12px */
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 2px;
  background: #fcfbf8;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.25);
}
@media (min-width: 769px) {
  .header {
    flex-direction: row;
  }
}
.header .header__shopping-basket {
  margin: 0;
  order: 1;
}
@media (min-width: 769px) {
  .header .header__shopping-basket {
    order: inherit;
    margin-right: 40px;
  }
}

/* MENU */
@keyframes anim-bar-top {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  50% {
    transform: translate3d(0, 10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
}
@keyframes anim-bar-top-rev {
  0% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
  50% {
    transform: translate3d(0, 10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
}
@keyframes anim-bar-middle {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}
@keyframes anim-bar-middle-rev {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes anim-bar-bottom {
  0% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  50% {
    transform: translate3d(0, -10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, -10px, 0) rotate(135deg);
  }
}
@keyframes anim-bar-bottom-rev {
  0% {
    transform: translate3d(0, -10px, 0) rotate(135deg);
  }
  50% {
    transform: translate3d(0, -10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
}
.menu {
  order: 2;
  margin: 30px 0;
}
@media (min-width: 769px) {
  .menu {
    order: inherit;
    margin: 0 0 0 40px;
  }
}

.drop-menu {
  display: flex;
  background: #a1f25f;
  width: 100%;
  height: auto;
  padding: 20px;
  position: absolute;
  top: 90px;
  left: 0;
  opacity: 1;
  transition: all 0.4s ease-in-out;
  /* Links hover animation */
}
.drop-menu .drop-menu__link {
  position: relative;
  color: #1f1b0f;
  margin-right: 50px;
}
.drop-menu .drop-menu__link:first-of-type {
  margin-left: 20px;
}
.drop-menu .drop-menu__link:last-of-type {
  margin: 0;
}
.drop-menu .drop-menu__link {
  transition: all 0.3s;
}
.drop-menu .drop-menu__link::before {
  content: "";
  display: block;
  background: #fcfbf8;
  height: 3px;
  width: 0;
  position: absolute;
  bottom: -1px;
  overflow: hidden;
  transition: all 0.3s;
}
.drop-menu .drop-menu__link:hover {
  transform: scale(1.05);
}
.drop-menu .drop-menu__link:hover::before {
  width: 100%;
  overflow: hidden;
}
.drop-menu.hidden {
  visibility: hidden;
  background: #fcfbf8;
  opacity: 0;
}

/* ANIMATIONS */
.bar-top,
.bar-middle,
.bar-bottom {
  background: #000;
  height: 4px;
  width: 40px;
  border-radius: 3px;
  margin-bottom: 6px;
}
.bar-top:last-of-type,
.bar-middle:last-of-type,
.bar-bottom:last-of-type {
  margin-bottom: 0;
}
.bar-top.not-active,
.bar-middle.not-active,
.bar-bottom.not-active {
  animation: none;
}

.bar-top {
  animation: anim-bar-top-rev 0.7s ease-out;
}

.bar-middle {
  animation: anim-bar-middle-rev 0.7s ease-out;
}

.bar-bottom {
  animation: anim-bar-bottom-rev 0.7s ease-out;
}

.active.bar-top {
  animation: anim-bar-top 0.7s ease forwards;
}
.active.bar-middle {
  animation: anim-bar-middle 0.7s ease forwards;
}
.active.bar-bottom {
  animation: anim-bar-bottom 0.7s ease forwards;
}